.burger-btn {
    @include for-tablet {
        display: none;
    }

    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;


    color: rgba(var(--cl-a),1.0);
    font-family: var(--font-family);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.burger {
    @include burger;

    color: rgba(var(--cl-b),1.0);
}

.mobile-menu {
    padding: 25px;
    overflow-y: auto;
    z-index: 25;
    transform: translateX(100%);
    position: fixed;
    top: var(--header-height);
    height: calc(100vh - var(--header-height));
    right: 0;
    background-color: rgba(var(--bg-c),1.0);
    transition: transform .2s;

    &.active {
        transform: translateX(0);
    }

    &__nav {

        > ul {
            display: flex;
            align-items: center;
            flex-direction: column;
            gap: 40px;
            padding-bottom: 50px;
            border-bottom: 1px solid var(--br-a);

             > li {
                color: rgba(var(--cl-a),1.0);
                text-align: center;

                font-family: var(--font-family2);
                font-size: 40px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
            }
        }

        aside {
            padding: 50px 0;
            border-bottom: 1px solid var(--br-a);

            h3 {
                display: none;
            }

            a {
                color: rgba(var(--cl-a),1.0);
                font-family: var(--font-family2);
                font-size: 19px;
                font-style: normal;
                font-weight: 700;
                line-height: 100%; /* 55.1px */
            }

            .count {
                display: none;
            }

            ul {
                display: flex;
                flex-direction: column;
                gap: 20px;
            }


        }
    }

    @include for-tablet {
        display: none;
    }

    &__bottom {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        padding: 50px 0 0;
        border-top: 1px solid var(--br-a);

        .nav-menu {
            display: flex;
            flex-direction: column;
            gap: 20px;

            a {
                color: rgba(var(--cl-a),1.0);
                font-family: var(--font-family);
                font-size: 12px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }
        }

        .footer__text {
            color: rgba(var(--cl-a),1.0);
        }
    }

    &__search {
        margin-bottom: 40px;
    }

}