.modal {
    max-width: 670px;
    width: 90vw;
    background-color: rgba(var(--bg-b),1.0);

    padding: 20px 20px 35px;

    &__inner {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .woocommerce-mini-cart__empty-message {
        grid-column: 1/-1;
        width: 100%;
        text-align: center;
    }

    .close {
        margin-left: auto;
    }

    .widgettitle {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin: 0 auto 40px;
        width: fit-content;

        color: rgba(var(--cl-e),1.0);
        text-align: center;
        font-family: var(--font-family2);
        font-size: clamp(22px, 2.817vw, 40px);
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        padding-bottom: 0;
        border-bottom: 6px solid var(--br-b);
    }

    .cart_list {
        grid-column: 1/-1;
        display: flex;
        flex-direction: column;
        width: 100%;
        gap: 20px;
    }

    .mini_cart_item {
        background: rgba(var(--bg-f),0.2);
        padding: 10px !important;
        display: grid;
        align-items: center;
        grid-template-columns: 50% auto 20px;
        gap: 10px;

        color: rgba(var(--cl-e),1.0);
        font-family: var(--font-family);
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;

        @include mobile {
            grid-template-columns: auto 20px;
            justify-content: space-between;
        }

        * {
            position: initial !important;
        }

        &::before,
        &::after {
            content: none !important;
        }

        .remove {
            order: 3;
            width: 20px;
            height: 20px;
            font-size: 0;

            background-image: var(--cart);
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }

        .quantity {
            order: 2;

            @include mobile {
                margin-left: 63px;
            }
        }

        a:not([class]) {
            display: grid;
            grid-template-columns: 50px auto;
            align-items: center;
            gap: 13px;

            img {
                float: none;
                order: -1;
                width: 100%;
                height: auto;
            }

            @include mobile {
                grid-column: 1/-1;
            }
        }


    }

    .widget_shopping_cart_content {
        display: grid;
        grid-template-columns: repeat(2, auto);
        justify-content: space-between;
        align-items: flex-start;
        gap: 25px;

        @include mobile {
            display: flex;
           
            align-items: center;
            flex-direction: column;
        }
    }

    .buttons {
        display: flex;
        flex-direction: column-reverse;
        gap: 10px;

        .button {
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            margin: 0 !important;

            font-family: var(--font-family2);
            font-size: 12px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 1.2px;
            text-transform: uppercase;
            width: 200px;
            height: 34px;
            border-radius: 0;

            &.checkout {
                background-color: rgba(var(--bg-a),1.0);
                color: rgba(var(--cl-b),1.0);
            }

            &:not(.checkout) {
                background-color: transparent;
                color: rgba(var(--cl-a),1.0);
                border: 1px solid  var(--br-a);
            }

        }
    }

    .total {
        margin: 0 !important;
        padding: 0 !important;
        border: 0 !important;
        display: flex;
        align-items: baseline;
        gap: 20px;

        color: rgba(var(--cl-e),1.0);
        font-family: var(--font-family2);
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        .amount {
            font-size: 40px;
            font-weight: 700;
        }
    }



    form {
        display: flex;
        flex-direction: column;
        gap: 20px;

        h4 {
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            margin: 0 auto 20px;
            width: fit-content;
    
            color: rgba(var(--cl-e),1.0);
            text-align: center;
            font-family: var(--font-family2);
            font-size: clamp(22px, 2.817vw, 40px);
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            padding-bottom: 0;
            border-bottom: 6px solid var(--br-b);
        }

        input:not([type="submit"]) {
            padding: 20px;
            width: 100%;
            display: flex;
            height: 65px;
            border: 1px solid #D4D4D4;

            color: rgba(var(--cl-a),1.0);
            font-family: var(--font-family2);
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }

        textarea {
            min-height: 150px;
            padding: 20px;
            width: 100%;
            display: flex;
            border: 1px solid #D4D4D4;

            color: rgba(var(--cl-a),1.0);
            font-family: var(--font-family2);
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }

        input[type="submit"] {
            display: flex;
            height: 60px;
            width: 200px;
            align-items: center;
            justify-content: center;
            background-color: rgba(var(--bg-a),1.0);
            margin: 0 auto;
            color: rgba(var(--cl-b),1.0);

            /* btn */
            font-family: var(--font-family2);
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 1.6px;
            text-transform: uppercase;
        }

    }

}



.wpcf7-spinner {
    display: none;          
} 