.phone {
    padding: 30px 0;

    a {
        color: rgba(var(--cl-a),1.0);
        font-family: var(--font-family2);
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__list {
        padding-top: 20px;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .accordion__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    .accordion__btn {

        svg {
            transition: all .4s;
        }

        &.active {
            svg {
                transform: scaleY(-1);
            }
        }
    }

}