.woocommerce-cart-form {

    * {
        border: none !important;
    }

    .product-thumbnail {
        display: flex !important;
    }

    td:before {
        content: none !important;
    }

    tbody tr {
        padding-right: 10px !important;
        background-color: rgba(var(--bg-f),0.2);


        input {
            height: 40px;
            // order: 2;
            width: 100% !important;
            background-color: rgba(var(--bg-b),1.0);
        }

        input[type="number"] {
            appearance: initial;
        }
    }

    .quantity {
        display: grid;
        grid-template-columns: 40px 60px 40px;


        button {
        position: relative;
        z-index: 10;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(var(--bg-b),1.0);

            &:nth-of-type(1) {
                order: -1;
            }

        }

    }

    .screen-reader-text {
        display: none;
    }

    td.product-quantity {
        padding: 0 !important;
    }
    thead tr {
        padding-right: 10px !important;
    }

    th.product-name {
        margin-left: -63px;
    }

    td {
        display: flex;
        align-items: center;

        * {
            color: rgba(var(--cl-e),1.0);

            font-family: var(--font-family);
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 24px; /* 150% */
        }
    }

    th {
        color: rgba(var(--cl-e),1.0);
        font-family: var(--font-family2);
        font-size: clamp(12px, 1.127vw, 16px);
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 1.6px;
        text-transform: uppercase;
    }

    tbody {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .product-remove {
        order: 6;
        padding: 0 !important;
    }

    thead tr,
    tbody tr {
        padding: 0;
        display: grid !important;
        grid-template-columns: 50px 1fr 140px 100px 1fr 20px;


        @include small-tablet {
            grid-template-columns: clamp(50px, 20.042vw, 100px) auto 100px;
            align-items: center;
            padding: 0 !important;

            .product-subtotal {
                display: none !important;
            }

            .product-remove {
                grid-row: 2;
                grid-column: 3;
                display: flex !important;
                align-items: center;
                justify-content: flex-end;
                padding: 10px !important;

                a {
                    width: 30px;
                    height: 30px;
                }
            }

            .product-quantity {
                grid-row: 1;
                grid-column: 2;
            }

            .product-name {
                display: flex !important;
                align-items: center;
                justify-content: flex-start;
                grid-row: 2;
                grid-column: 1/span 2;
            }

            .product-thumbnail {
                img {
                    width: 100%;
                }
            }
        }
    }

    th:not(.product-name),
    td:not(.product-name) { 
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    tbody tr:not([class]) {
        display: none !important;
    }

    .product-remove a {
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0;
        background-image: var(--cart) !important;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: initial;

        &:hover {
            background-color: initial;
            border-radius: initial;
            transform: scale(1);
        }

    }

}

.cart-collaterals {
    width: fit-content !important;
    margin-left: auto;
    padding: 20px 60px 20px;
    background-color: rgba(var(--bg-f),1.0);

    @include small-tablet {
        margin: 0 auto;
    }

    * {
        padding: 0 !important;
        border: 0 !important;
    }

    .cart_totals {
        width: 100% !important;
        float: none;
    }

    h2,
    .cart-subtotal,
    th
     {
        display: none !important;
    }

    [data-title] {
        display: flex !important;
        align-items: center;
        justify-content: center;
        color: rgba(var(--cl-c),1.0);
        font-family: var(--font-family2);
        font-size: 40px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        background-color: transparent !important;

        &::before {
            content: none !important;
        }
    }

    .checkout-button {
        margin: 0 !important;
        max-width: 322px;
        width: 90vw;
        display: flex !important;
        align-items: center;
        justify-content: center;
        height: 60px;
        background-color: rgba(var(--bg-a),1.0) !important;
        border-radius: 0 !important;

        color: rgba(var(--cl-b),1.0);

        font-family: var(--font-family2);
        font-size: 16px !important;
        font-style: normal;
        font-weight: 700;
        line-height: normal !important;
        letter-spacing: 1.6px !important;
        text-transform: uppercase;
    }
}